.program {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 50px rgba(51, 62, 72, 0.1);
    align-items: center;
    margin: 26px auto;
    background: rgba(228, 228, 228, 0.96);
}

.program__content {
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-self:flex-start;
}

.program__content-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 8px;
    align-self: flex-start;
}

.program__content-body {
    font-weight: 500;
    align-self: flex-start;
}

.program__content-url {
    font-weight: 500;
    align-self: flex-start;
}

.program__content-url a {
    text-decoration: none;
    font-weight: 600;
    color: rgb(237, 28, 36);
}

.program__btn {
    align-self: flex-end;
}